<template>
  <v-container class="">
    <v-layout
      justify-center
      style="height: 600px"
      align-center
      class="white--text"
    >
      <v-flex class="" xs12 md6>
        <v-card :loading="carga" color="rgba(0,0,0,0.3)" class="white--text">
          <v-card-title>
            <v-layout justify-center>
              <h1 class="font-weight-regular">Inicio Sesion</h1>
            </v-layout>
          </v-card-title>
          <v-card-text>
            <v-form>
              <v-layout>
                <p class="text-center white--text text-caption">
                  No De Trabajador
                </p>
              </v-layout>
              <v-layout>
                <v-otp-input
                  v-model="usuario"
                  length="7"
                  type="number"
                  dark
                ></v-otp-input>
              </v-layout>
              <v-layout>
                <v-text-field
                  prepend-icon="mdi-lock"
                  label="Contrase√±a"
                  v-model="contrase√±a"
                  @click:append="show1 = !show1"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show1 ? 'text' : 'password'"
                  dark
                >
                </v-text-field>
              </v-layout>
                  <v-btn small color="white" :to="{ path: '/recuperar_contrasenia' }" text> Recuperar Contrase√±a</v-btn>
              
              
            </v-form>
            <v-card-actions class="ma-0 pa-0" v-if="termino">
              <v-col class="ma-0 pa-0 text-center">
                
                <div class="text-xs-center">
                  <v-btn
                    @click="Inciar_Sesion();"
                    outlined
                    rounded
                    color="light-green accent-3"
                  >
                    Iniciar
                  </v-btn>
                </div>
              </v-col>
            </v-card-actions>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
    
  </v-container>
</template>

<script>
import axios from "axios";
import { mapMutations , mapState} from "vuex";


export default {


  components: { },
  state: {},
  mounted() {
    this.Comprober()
    
  },
  props: {},
  data() {
    return {
      usuario: "",
      contrase√±a: "",
      show1: false,
      carga: false,
      con: false,  
    };
  },
  watch: {},
  methods: {
    ...mapMutations(["Modificar_cargar"
    , "Modificar_login",
    "Actualizar_Nombre"
    ,"Actualizar_No_Trabajador"
    ,"Actualizar_Tipo_User",
    "Actualizar_No_JD",
    "Actualizar_Token"
    ,"Checar_Token",
    "Salir",
    "Modificar_Informacion",
    "Modificar_Espera",
    "Modificar_Correcto",
    "Modificar_Datos",
    "Modificar_Boton",
    "Modificar_ruta"
    ]),
    Comprober(){
      if(localStorage.getItem("Token")){
        this.Modificar_login(true);        
        this.Actualizar_Token(localStorage.getItem("token"));  
        this.Cargar_Datos();  
        this.$router.push("/calendario");
      }
    },

    Iniciar() {
      this.carga = !this.carga;
    },
    async Inciar_Sesion() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Comprobando Usuario Y Contrase√±a")      
      this.Modificar_Espera(true);
      this.Modificar_Correcto(false);
      this.Modificar_ruta("/calendario");
      this.Modificar_Boton(false);      
      const json = JSON.stringify({
        id:this.usuario,        
        pass: this.contrase√±a,
      });
      try {
        let res = await axios.post("http://"+this.ip+"/api/docente/login", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        })
        
        if(!this.error && !res.data.Error){          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);          
          this.Modificar_Informacion("Bienvenido "+ res.data.Nombre);  
          this.Modificar_login(true);          
          this.Actualizar_No_Trabajador(res.data.No_Empleado+"");
          this.Actualizar_Nombre(res.data.Nombre)     
          this.Actualizar_Tipo_User(res.data.User_Type)
          this.Actualizar_No_JD(res.data.No_JD)
          this.Actualizar_Token(res.data.Token)
          
          localStorage.setItem("Token",res.data.Token)
          this.Modificar_Boton(true);
        }
        else if(res.data.Error){
          this.Modificar_Espera( false);
          this.Modificar_Correcto(false);            
          this.Modificar_Datos(res.data.status);
          this.Modificar_ruta("/login");
          this.Modificar_Boton(true);
        }  
      } catch (error) {
        this.Modificar_Espera( false);
          this.Modificar_Correcto(false);                      
          this.Modificar_Informacion(error);
      }                
    },
    async Cargar_Datos() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Comprobando Usuario Y Contrase√±a")
      this.Modificar_Espera(true);      
      const json = JSON.stringify({
        token : localStorage.getItem("Token")
      });
      
      try {
        let res = await axios.post("http://"+this.ip+"/api/docente/datos", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        })        
        if(!this.error && !res.data.Error){      
             
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);          
          this.Modificar_Informacion("Bienvenido "+ res.data.Datos.Nombre);  
          this.Modificar_login(true);          
          this.Actualizar_No_Trabajador(res.data.Datos.No_Empleado+"");
          this.Actualizar_Nombre(res.data.Datos.Nombre)     
          this.Actualizar_Tipo_User(res.data.Datos.User_T)          
          this.Actualizar_No_JD(res.data.Datos.No_JD)                    
          this.Modificar_Boton(true)
        }
        else if(res.data.Error){
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);  
          this.error=true;
          this.Modificar_Datos(res.data.status);
          this.Modificar_Boton(true)
        }  
      } catch (error) {
        this.Modificar_Espera(false);
          this.Modificar_Correcto(false);  
          this.Modificar_Boton(true)
          console.log(error);
          this.Salir();
      }          
    }
  },
  computed:{
      ...mapState(['login','ip']),
      termino(){
        let u = ""
        u.length
        return this.usuario.length == 7 && this.contrase√±a != ""
      }
  }
  //<h1>{{this.$route.name}}</h1>
};
</script>
